import { createSlice } from "@reduxjs/toolkit";
import { RankingListRow } from "../types";

export interface RankingState {
    rankings: RankingListRow[];
    error: null | string;
}

const initialState: RankingState = {
    rankings: [],
    error: null,
};

interface SetAction<PayloadType> {
    type: string;
    payload: PayloadType;
}

const ranking = createSlice({
    name: "ranking",
    initialState,
    reducers: {
        setRankings: (state, action: SetAction<RankingListRow[]>) => {
            state.rankings = action.payload;
        },
        setError: (state, action: SetAction<string | null>) => {
            state.error = action.payload;
        },
    },
});

export const actions = ranking.actions;

export default ranking.reducer;
