import { RootState } from "../slices";

export const getAthletes = (state: RootState) => state.analysis.athletes;

export const getPerformances = (state: RootState) =>
    state.analysis.performances;

export const getAnalysisType = (state: RootState) =>
    state.analysis.analysisType;

export const getVenues = (state: RootState) => state.analysis.venues;

export const getMeetings = (state: RootState) => state.analysis.meetings;

export const getError = (state: RootState) => state.analysis.error;

export const getSelectedAthleteIds = (state: RootState) =>
    state.analysis.selectedAthleteIds;

export const getAthleteById = (state: RootState, id?: string) =>
    state.analysis.athletes.find((athlete) => athlete.id === id);

export const getRankingsByAthleteId = (state: RootState, id?: string) =>
    state.analysis.rankings
        .filter(({ athleteId }) => athleteId === id)
        .sort((a, b) => parseInt(b.year) - parseInt(a.year));

export const getVenueById = (state: RootState, id?: string) =>
    state.analysis.venues.find((venue) => venue.id === id);

export const getAthleteIds = (state: RootState) =>
    state.analysis.athletes.map(({ id }) => id);

export const getPerformancesForIndividualAnalysis = (state: RootState) =>
    state.analysis.performances.filter(
        ({ athleteId }) => athleteId === state.analysis.selectedAthleteIds[0]
    );
