import { useSelector } from "react-redux";
import Table from "../../components/Table/Table";
import { sortPerformancesBestToWorst } from "../../sorts";
import { Athlete, Performance } from "../../types";
import { getSelectedEvent } from "../../selectors/view";
import { formatPerformance } from "../../formatters";

const BestPerformancesTable = ({
    athleteA,
    athleteB,
    performances,
}: {
    athleteA: Athlete;
    athleteB: Athlete;
    performances: Performance[];
}) => {
    const selectedEvent = useSelector(getSelectedEvent);
    const data = [
        performances
            .filter(
                ({ athleteId, event }) =>
                    athleteId === athleteA.id && event === selectedEvent
            )
            .sort(sortPerformancesBestToWorst)
            .slice(0, 10)
            .map(({ performance, event }) =>
                formatPerformance(performance, event)
            ),
        performances
            .filter(
                ({ athleteId, event }) =>
                    athleteId === athleteB.id && event === selectedEvent
            )
            .sort(sortPerformancesBestToWorst)
            .slice(0, 10)
            .map(({ performance, event }) =>
                formatPerformance(performance, event)
            ),
    ];
    return (
        <Table
            headers={[athleteA.name, athleteB.name]}
            data={data}
            title={"Best Performances"}
        />
    );
};

export default BestPerformancesTable;
