import { Performance } from "./types";

export const sortPerformancesByDate = (a: Performance, b: Performance) =>
    b.date - a.date;

export const sortPerformancesBestToWorst = (a: Performance, b: Performance) => {
    if (
        typeof a.performance === "number" &&
        typeof b.performance === "number"
    ) {
        return a.performance - b.performance;
    }
    return typeof a.performance === "number" ? -1 : 1;
};
