import Search from "../../components/Search/Search";
import Modal from "../../components/Modal/Modal";
import { SearchResultType } from "../../components/Search/Result";
import { isMobile } from "../../App";
import { fetchAthleteSearchResults } from "../../thunks";
import { AthleteSearchResult } from "../../types";
import styles from "./athlete.module.css";

const AthleteSearch = () => {
    const dataToResultFormat = (
        data: AthleteSearchResult[]
    ): SearchResultType[] =>
        data.map((d) => ({
            id: d.id,
            primary: d.name,
            secondary: d.club,
            sub: d.ageGroup,
        }));
    return (
        <div className={styles.athletePage}>
            {isMobile && (
                <Search
                    headers={["Name", "Club"]}
                    fetchAction={fetchAthleteSearchResults}
                    dataToResultFormat={dataToResultFormat}
                    entity="athlete"
                />
            )}
            {!isMobile && (
                <Modal title="Athlete Search" closeAction={() => {}}>
                    <Search
                        headers={["Name", "Club"]}
                        fetchAction={fetchAthleteSearchResults}
                        dataToResultFormat={dataToResultFormat}
                        entity="athlete"
                    />
                </Modal>
            )}
        </div>
    );
};

export default AthleteSearch;
