import * as d3 from "d3";
import styles from "./chart.module.css";
import { ReactNode, useEffect } from "react";

interface DataPoint {
    x: number;
    y: number;
}

interface ChartProps {
    title: string;
    data: {
        point: DataPoint[];
        bar?: DataPoint[];
    };
    formatters: {
        x?: (d: number) => string;
        y?: (d: number) => string;
    };
    rangeOverrides?: {
        x?: { min: number; max: number };
        y?: { min: number; max: number };
    };
    controlPanel?: ReactNode;
}

const plotMargins = { x: 30, y: 20 };

const Chart = ({
    title,
    data,
    formatters,
    rangeOverrides,
    controlPanel,
}: ChartProps) => {
    const drawGraph = (data: Record<string, DataPoint[]>) => {
        // @ts-ignore
        const { width, height } = d3
            .select("#chart")
            .node()
            // @ts-ignore
            .getBoundingClientRect();
        const xMax = d3.max(data.point, (d) => d.x) as number;
        const xMin = d3.min(data.point, (d) => d.x) as number;
        const yMax = d3.max(data.point, (d) => d.y) as number;
        const yMin = d3.min(data.point, (d) => d.y) as number;

        const yRange = yMax - yMin;

        const xScale = d3
            .scaleLinear()
            .domain([
                (rangeOverrides?.x?.min ?? xMin) * 0.999,
                (rangeOverrides?.x?.max ?? xMax) * 1.001,
            ])
            .range([plotMargins.x, width - plotMargins.x]);
        const yScale = d3
            .scaleLinear()
            .domain([
                (rangeOverrides?.y?.min ?? yMin) * 0.999,
                (rangeOverrides?.y?.max ?? yMax) * 1.001,
            ])
            .range([height - plotMargins.y, plotMargins.y]);

        const graph = d3.select("#chart");
        graph.selectAll("*").remove();
        graph.attr("width", width).attr("height", height);

        const xAxis = d3
            .axisBottom(xScale)
            .ticks(10)
            .tickSize(-height + plotMargins.x)
            .tickFormat((d) =>
                formatters?.x ? formatters.x(d as number) : d.toString()
            );
        // .tickFormat((d, i) =>
        //     i % 2
        //         ? isForSeason
        //             ? formatShortMonth(d as number)
        //             : formatYear(d as number)
        //         : ""
        // );
        const yAxis = d3
            .axisLeft(yScale)
            .ticks(5)
            .tickSize(-width + 3 * plotMargins.y);
        // .tickFormat((d) =>
        //     performanceRange < 1.5 && (d as number) < 100
        //         ? formatPerformance(d as number)
        //         : formatPerformance(d as number).slice(
        //               0,
        //               (d as number) < 100 ? -1 : -3
        //           )
        // );

        // add axes
        graph
            .append("g")
            .attr("transform", `translate(0, ${height - plotMargins.y})`)
            .call(xAxis);

        graph
            .append("g")
            .attr("transform", `translate(${plotMargins.x},0)`)
            .call(yAxis);

        if (data?.point) {
            graph
                .selectAll(".point")
                .data(data.point)
                .enter()
                .append("circle")
                .attr("r", 3)
                .attr("cx", (d) => xScale(d.x))
                .attr("cy", (d) => yScale(d.y))
                .attr("class", "point")
                .attr("fill", "black")
                .exit()
                .remove();
        }

        if (data?.bar) {
            const yMaxBar = d3.max(data.bar, (d) => d.y) as number;
            const yMinBar = d3.min(data.bar, (d) => d.y) as number;
            const yScaleBar = d3
                .scaleLinear()
                .domain([
                    rangeOverrides?.y?.min ?? yMinBar,
                    rangeOverrides?.y?.max ?? yMaxBar,
                ])
                .range([height - plotMargins.y, plotMargins.y]);
            graph
                .selectAll(".bar")
                .data(data.bar)
                .enter()
                .append("rect")
                .attr("fill", "tomato")
                .attr("opacity", 0.5)
                .attr("x", (d) => xScale(d.x))
                .attr("y", (d) => yScaleBar(d.y) - plotMargins.y)
                .attr("width", width / 13)
                .attr("height", (d) => height - yScaleBar(d.y))
                .exit();
        }
    };

    useEffect(() => {
        drawGraph(data);
    });

    return (
        <div className={styles.container}>
            <div className={styles.title}>{title}</div>
            <svg id={"chart"} className={styles.chart}></svg>
            {controlPanel && (
                <div className={styles.controlsContainer}>{controlPanel}</div>
            )}
        </div>
    );
};

export default Chart;
