import { formatPercentage } from "../../../formatters";
import styles from "./headToHeadRow.module.css";

interface HeadToHeadRowProps {
    name: string;
    a?: number;
    b?: number;
    formattedA?: string;
    formattedB?: string;
    formatValueAs?: "percentage";
}

const getFormattedValue = (value: number, formatter?: string) => {
    if (formatter === "percentage") {
        return formatPercentage(value);
    } else {
        return value;
    }
};

const HeadToHeadRow = ({
    name,
    a = 0,
    b = 0,
    formattedA,
    formattedB,
    formatValueAs,
}: HeadToHeadRowProps) => {
    const { aBarWidth, bBarWidth } =
        a + b > 0
            ? {
                  aBarWidth:
                      formatValueAs === "percentage" ? a : (a / (a + b)) * 100,
                  bBarWidth:
                      formatValueAs === "percentage" ? b : (b / (a + b)) * 100,
              }
            : {
                  aBarWidth: 0,
                  bBarWidth: 0,
              };
    return (
        <div className={styles.row}>
            <div className={styles.valueName}>{name}</div>
            <div className={styles.bars}>
                <div className={styles.barContainer}>
                    <div
                        className={styles.remainder}
                        style={{ width: `${100 - aBarWidth}%` }}
                    ></div>
                    <div
                        className={styles.bar}
                        style={{ width: `${aBarWidth}%` }}
                    ></div>
                </div>
                <div className={styles.barDivider}></div>
                <div className={styles.barContainer}>
                    <div
                        className={styles.bar}
                        style={{
                            width: `${bBarWidth}%`,
                            backgroundColor: "#6347ff",
                        }}
                    ></div>
                    <div
                        className={styles.remainder}
                        style={{ width: `${100 - bBarWidth}%` }}
                    ></div>
                </div>
            </div>
            <div className={styles.values}>
                <div className={styles.value}>
                    {formattedA ?? getFormattedValue(a, formatValueAs)}
                </div>
                <div className={styles.value}>
                    {formattedB ?? getFormattedValue(b, formatValueAs)}
                </div>
            </div>
        </div>
    );
};

export default HeadToHeadRow;
