import { useState } from "react";
import { Link } from "react-router-dom";
import { isMobile } from "../../App";
import styles from "./header.module.css";

export const MenuIcon = () => (
    <div className={styles.menuIcon}>
        <div className={styles.bar}></div>
        <div className={styles.bar}></div>
        <div className={styles.bar}></div>
    </div>
);

export const MenuContent = ({
    showMenuContent = true,
}: {
    showMenuContent?: boolean;
}) => (
    <div
        className={`${
            isMobile ? styles.menuContent : styles.menuContentDesktop
        } ${showMenuContent ? "" : styles.hide}`}
    >
        <Link
            className={isMobile ? styles.menuItem : styles.menuItemDesktop}
            to="/"
        >
            Home
        </Link>
        <Link
            className={isMobile ? styles.menuItem : styles.menuItemDesktop}
            to="/rankings/2023/100/ALL/M"
        >
            Rankings
        </Link>
        <Link
            className={isMobile ? styles.menuItem : styles.menuItemDesktop}
            to="/athlete"
        >
            Athlete
        </Link>
        <Link
            className={isMobile ? styles.menuItem : styles.menuItemDesktop}
            to="/meeting"
        >
            Meeting
        </Link>
        <Link
            className={isMobile ? styles.menuItem : styles.menuItemDesktop}
            to="/headtohead"
        >
            Head To Head
        </Link>
        <Link
            className={isMobile ? styles.menuItem : styles.menuItemDesktop}
            to="/news"
        >
            News
        </Link>
        <Link
            className={isMobile ? styles.menuItem : styles.menuItemDesktop}
            to="/contact"
        >
            Contact
        </Link>
    </div>
);

const HeaderMenu = () => {
    const [showMenuContent, setShowMenuContent] = useState(false);

    const toggleShowMenuContent = () => {
        setShowMenuContent(!showMenuContent);
    };

    return (
        <div className={styles.headerMenu} onClick={toggleShowMenuContent}>
            <MenuIcon />
            <MenuContent showMenuContent={showMenuContent} />
            <div
                className={`${styles.appMenuBlanket} ${
                    showMenuContent ? "" : styles.hidden
                }`}
            ></div>
        </div>
    );
};

export default HeaderMenu;
