import { useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import HeadToHeadRow from "../../components/Rows/HeadToHeadRow/HeadToHeadRow";
import ResultComparisonRow from "../../components/Rows/ResultComparisonRow/ResultComparisonRow";
import { Athlete, Performance } from "../../types";
import { getEventStatistics, getPerformancesByEvent } from "../../queries";
import { formatPerformance } from "../../formatters";
import { getPerformances } from "../../selectors/analysis";
import { actions } from "../../slices/view";
import { getSelectedEvent } from "../../selectors/view";
import styles from "./headToHead.module.css";

interface DataComparisonSectionProps {
    athleteA: Athlete;
    athleteB: Athlete;
    comparisonSheet: Record<string, Record<string, Record<string, number>>>;
    headToHeadPerformances: Performance[][];
}

const DataComparisonSection = ({
    athleteA,
    athleteB,
    comparisonSheet,
    headToHeadPerformances,
}: DataComparisonSectionProps) => {
    const dispatch = useDispatch();
    const allPerformances = useSelector(getPerformances);
    const selectedEvent = useSelector(getSelectedEvent);

    const handleSelectEvent = (event: React.ChangeEvent<HTMLSelectElement>) => {
        dispatch(actions.setSelectedEvent(event.target.value));
    };

    // check this not same as below
    if (
        !(
            athleteA &&
            athleteB &&
            Object.keys(comparisonSheet).length &&
            comparisonSheet[athleteA.id][athleteB.id] &&
            Object.keys(comparisonSheet[athleteA.id][athleteB.id]).length > 0
        )
    ) {
        return null;
    }

    // check this
    if (
        Object.keys(comparisonSheet).length &&
        Object.keys(comparisonSheet[athleteA.id][athleteB.id]).length === 0
    ) {
        return <div>Never competed!</div>;
    }

    const eventStatsAthleteA = getEventStatistics({
        ...getPerformancesByEvent(
            allPerformances.filter((p) => p.athleteId === athleteA.id)
        ),
        ALL: allPerformances.filter((p) => p.athleteId === athleteA.id),
    });
    const eventStatsAthleteB = getEventStatistics({
        ...getPerformancesByEvent(
            allPerformances.filter((p) => p.athleteId === athleteB.id)
        ),
        ALL: allPerformances.filter((p) => p.athleteId === athleteB.id),
    });
    return (
        eventStatsAthleteA &&
        eventStatsAthleteB && (
            <>
                <div className={styles.eventSelect}>
                    Select Event
                    <select value={selectedEvent} onChange={handleSelectEvent}>
                        {athleteA &&
                            athleteB &&
                            Object.keys(comparisonSheet).length &&
                            Object.keys(
                                comparisonSheet[athleteA.id][athleteB.id]
                            ).map((event) => (
                                <option value={event}>{event}</option>
                            ))}
                    </select>
                </div>
                {(comparisonSheet[athleteA.id][athleteB.id][selectedEvent] >
                    0 ||
                    comparisonSheet[athleteB.id][athleteA.id][selectedEvent] >
                        0) && (
                    <HeadToHeadRow
                        name="Head To Head Wins"
                        a={
                            comparisonSheet[athleteA.id][athleteB.id][
                                selectedEvent
                            ]
                        }
                        b={
                            comparisonSheet[athleteB.id][athleteA.id][
                                selectedEvent
                            ]
                        }
                    />
                )}
                {selectedEvent !== "ALL" && (
                    <HeadToHeadRow
                        name="Personal Best"
                        a={eventStatsAthleteA[selectedEvent]?.best}
                        b={eventStatsAthleteB[selectedEvent]?.best}
                        formattedA={formatPerformance(
                            eventStatsAthleteA[selectedEvent]?.best,
                            selectedEvent
                        )}
                        formattedB={formatPerformance(
                            eventStatsAthleteB[selectedEvent]?.best,
                            selectedEvent
                        )}
                    />
                )}
                {eventStatsAthleteA[selectedEvent]?.seasonBest &&
                    eventStatsAthleteB[selectedEvent]?.seasonBest &&
                    selectedEvent !== "ALL" && (
                        <HeadToHeadRow
                            name="Season Best"
                            a={
                                eventStatsAthleteA[selectedEvent]?.seasonBest ??
                                0
                            }
                            b={
                                eventStatsAthleteB[selectedEvent]?.seasonBest ??
                                0
                            }
                            formattedA={formatPerformance(
                                eventStatsAthleteA[selectedEvent]?.seasonBest ??
                                    0,
                                selectedEvent
                            )}
                            formattedB={formatPerformance(
                                eventStatsAthleteB[selectedEvent]?.seasonBest ??
                                    0,
                                selectedEvent
                            )}
                        />
                    )}
                <HeadToHeadRow
                    name="Total Performances"
                    a={eventStatsAthleteA[selectedEvent]?.totalPerformances}
                    b={eventStatsAthleteB[selectedEvent]?.totalPerformances}
                />
                <HeadToHeadRow
                    name="Win Count"
                    a={eventStatsAthleteA[selectedEvent]?.winCount}
                    b={eventStatsAthleteB[selectedEvent]?.winCount}
                />
                <HeadToHeadRow
                    name="Longest Win Streak"
                    a={eventStatsAthleteA[selectedEvent]?.longestWinStreak}
                    b={eventStatsAthleteB[selectedEvent]?.longestWinStreak}
                />
                <HeadToHeadRow
                    name="Win Rate"
                    a={eventStatsAthleteA[selectedEvent]?.winRate}
                    b={eventStatsAthleteB[selectedEvent]?.winRate}
                    formatValueAs="percentage"
                />
                <HeadToHeadRow
                    name="Top 3 Rate"
                    a={eventStatsAthleteA[selectedEvent]?.topThreeRate}
                    b={eventStatsAthleteB[selectedEvent]?.topThreeRate}
                    formatValueAs="percentage"
                />
                <div className={styles.results}>
                    {headToHeadPerformances.length > 0 && <div>Results</div>}
                    {headToHeadPerformances.map((ps) =>
                        ps[0].event === selectedEvent ||
                        selectedEvent === "ALL" ? (
                            <ResultComparisonRow
                                performances={ps}
                                event={
                                    selectedEvent === "ALL"
                                        ? ps[0].event
                                        : undefined
                                }
                            />
                        ) : null
                    )}
                </div>
            </>
        )
    );
};

export default DataComparisonSection;
