import { MEDAL } from "../../constants";
import styles from "./athlete.module.css";

const MedalTally = ({ medals }: { medals: Record<string, MEDAL[]> }) => (
    <div className={styles.athleteMedals}>
        {Object.entries(medals).map(([c, ms]) => (
            <div className={styles.championshipMedals}>
                {c}
                {ms.map((m) => (
                    <div className={styles.medal}>{m}</div>
                ))}
            </div>
        ))}
    </div>
);

export default MedalTally;
