import { useDispatch, useSelector } from "react-redux";
import AthletePagePerformanceRow from "./AthletePagePerformanceRow";
import MedalTally from "./MedalTally";
import DropdownSelector from "../../components/DropdownSelector/DropdownSelector";
import AthleteChart from "./AthleteChart";
import { actions } from "../../slices/view";
import { getChampionshipMedals } from "../../queries";
import { sortPerformancesByDate } from "../../sorts";
import { Performance, Venue } from "../../types";
import { getSeason, getSelectedEvent } from "../../selectors/view";
import styles from "./overviewDesktop.module.css";
import List from "../../components/List/List";

interface OverviewDesktopProps {
    performances: Performance[];
    events: string[];
    venues: Venue[];
    seasons: string[];
}

const OverviewDesktop = ({
    performances,
    events,
    venues,
    seasons,
}: OverviewDesktopProps) => {
    const dispatch = useDispatch();
    const selectedEvent = useSelector(getSelectedEvent);
    const season = useSelector(getSeason);

    return (
        <div className={styles.overviewDesktop}>
            <div className={styles.selectorsBar}>
                <DropdownSelector
                    selectedOption={selectedEvent}
                    text={"Event"}
                    options={["ALL", ...events]}
                    onSelectAction={(id: string) =>
                        dispatch(actions.setSelectedEvent(id))
                    }
                />
                <DropdownSelector
                    selectedOption={season}
                    text={"Season"}
                    options={seasons}
                    onSelectAction={(season) =>
                        dispatch(actions.setSeason(season))
                    }
                />
            </div>
            <div className={styles.analysis}>
                <div className={styles.leftAnalysis}>
                    <List title="Recent Performances">
                        {performances.length > 0 &&
                            performances
                                .sort(sortPerformancesByDate)
                                .map((p) => (
                                    <AthletePagePerformanceRow
                                        performance={p}
                                        eventName={p.event}
                                        venue={venues.find(
                                            (v) => v.id === p.venueId
                                        )}
                                    />
                                ))}
                    </List>
                </div>
                <div className={styles.mainAnalysis}>
                    <AthleteChart performances={performances} />
                </div>
                <div className={styles.rightAnalysis}>
                    <MedalTally medals={getChampionshipMedals(performances)} />
                </div>
            </div>
        </div>
    );
};

export default OverviewDesktop;
