import { useSelector } from "react-redux";
import { useState } from "react";
import Search from "../Search/Search";
import { SearchResultType } from "../Search/Result";
import { fetchAthleteData, fetchAthleteSearchResults } from "../../thunks";
import { getAthletes, getMeetings, getVenues } from "../../selectors/analysis";
import { AthleteSearchResult } from "../../types";
import Modal from "../Modal/Modal";
import styles from "./selectAthleteZone.module.css";

const dataToResultFormat = (data: AthleteSearchResult[]): SearchResultType[] =>
    data.map((d) => ({
        id: d.id,
        primary: d.name,
        secondary: d.club,
        sub: d.ageGroup,
    }));

interface SelectAthleteZoneProps {
    selectedIds: Record<string, string | undefined>;
    onAthleteCardClick: (
        id: string
    ) => (_e: React.MouseEvent<HTMLElement>) => void;
}

const SelectAthleteZone = ({
    selectedIds,
    onAthleteCardClick,
}: SelectAthleteZoneProps) => {
    const athletes = useSelector(getAthletes);
    const allMeetings = useSelector(getMeetings);
    const allVenues = useSelector(getVenues);
    const [showSearch, setShowSearch] = useState(false);

    const handleResultClick = (id: string) => () => {
        if (!athletes.map(({ id }) => id).includes(id)) {
            fetchAthleteData(id, allMeetings, allVenues);
        }
        setShowSearch(false);
    };

    return (
        <>
            <div className={styles.selectAthleteZone}>
                {athletes.map((athlete) => (
                    <div
                        className={`${styles.athleteCard} ${
                            selectedIds.A === athlete.id ||
                            selectedIds.B === athlete.id
                                ? styles.selected
                                : ""
                        }`}
                        onClick={onAthleteCardClick(athlete.id)}
                    >
                        {athlete.name}
                    </div>
                ))}
                <div
                    className={`${styles.athleteCard} ${styles.addCard}`}
                    onClick={() => setShowSearch(true)}
                >
                    {`\u002B`}
                </div>
            </div>
            {showSearch && (
                <Modal
                    title="Athlete Search"
                    closeAction={() => setShowSearch(false)}
                >
                    <Search
                        headers={["Name", "Club"]}
                        fetchAction={fetchAthleteSearchResults}
                        dataToResultFormat={dataToResultFormat}
                        entity="athlete"
                        handleResultClick={handleResultClick}
                    />
                </Modal>
            )}
        </>
    );
};

export default SelectAthleteZone;
