import { useEffect, useState } from "react";
import { SearchResultType } from "./Result";
import Results from "./Results";
import LoadingIcon from "../LoadingIcon/LoadingIcon";
import styles from "./search.module.css";

interface SearchProps {
    headers: string[];
    fetchAction: (...params: any) => Promise<any[]>;
    dataToResultFormat: (params: any[]) => SearchResultType[];
    handleResultClick?: (id: string) => () => void;
    isImmediateSearch?: boolean;
    entity: string;
}

const Search = ({
    headers,
    fetchAction,
    dataToResultFormat,
    entity,
    handleResultClick,
    isImmediateSearch = false,
}: SearchProps) => {
    const [results, setResults] = useState<SearchResultType[]>([]);
    const [filteredResults, setFilteredResults] = useState<SearchResultType[]>(
        []
    );
    const [searchTerm, setSearchTerm] = useState("");
    const [isSearching, setIsSearching] = useState(false);
    const handleTermChange = (event: React.ChangeEvent<HTMLInputElement>) => {
        if (isImmediateSearch) {
            setFilteredResults(
                results.filter(
                    (r) =>
                        r.primary
                            .toString()
                            .toLowerCase()
                            .includes(event.target.value) ||
                        r.secondary
                            ?.toString()
                            .toLowerCase()
                            .includes(event.target.value) ||
                        r.sub
                            ?.toString()
                            .toLowerCase()
                            .includes(event.target.value)
                )
            );
        } else {
            setSearchTerm(event.target.value);
        }
    };
    const handleSearch = async () => {
        setIsSearching(true);
        setResults([]);
        const [term1, ...rest] = searchTerm.trim().split(" ");
        const term2 = rest.join(" ");
        fetchAction(term2, term1, "").then((data) => {
            setResults(dataToResultFormat(data));
            setIsSearching(false);
        });
    };
    useEffect(() => {
        if (isImmediateSearch) {
            setIsSearching(true);
            fetchAction().then((data) => {
                setResults(dataToResultFormat(data));
                setIsSearching(false);
            });
        }
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, []);
    useEffect(() => {
        setFilteredResults(results);
    }, [results]);
    return (
        <div className={styles.searchPage}>
            <div className={styles.searchInput}>
                <input
                    type="text"
                    onChange={handleTermChange}
                    onKeyDown={(event) => {
                        if (event.key === "Enter") {
                            handleSearch();
                        }
                    }}
                />
                {!isImmediateSearch && (
                    <button onClick={handleSearch}>Search</button>
                )}
            </div>
            {isSearching && (
                <div className={styles.searchLoading}>
                    <LoadingIcon />
                    <div>Searching for {entity}...</div>
                </div>
            )}
            {!isSearching && (
                <div className={styles.searchResults}>
                    {filteredResults.length > 0 && (
                        <>
                            <div className={styles.searchResultsHeader}>
                                {headers.map((h) => (
                                    <div key={h}>{h}</div>
                                ))}
                            </div>
                            <Results
                                results={filteredResults}
                                entity={entity}
                                handleResultClick={handleResultClick}
                            />
                        </>
                    )}
                </div>
            )}
        </div>
    );
};

export default Search;
