import { Route, Routes } from "react-router-dom";
import Header from "./components/Header/Header";
import AthleteSearch from "./pages/Athlete/AthleteSearch";
import Contact from "./pages/Contact/Contact";
import AthletePage from "./pages/Athlete/Athlete";
import Home from "./pages/Home/Home";
import HeadToHeadPage from "./pages/HeatToHead/HeadToHead";
import NewsPage from "./pages/News/News";
import MeetingPage from "./pages/Meeting/MeetingPage";
import MeetingSearch from "./pages/Meeting/MeetingSearch";
import "./index.css";
import RankingPage from "./pages/Ranking/Ranking";

export const isMobile = true; // temp whilst we haven't finished desktop yet
// window.innerWidth < 800 && window.innerHeight / window.innerWidth > 1.5;

const App = () => (
    <div className="app">
        <Header />
        <div className="content">
            <Routes>
                <Route path="/" element={<Home />} />
                <Route path="/athlete" element={<AthleteSearch />} />
                <Route path="/athlete/:id" element={<AthletePage />} />
                <Route path="/meeting" element={<MeetingSearch />} />
                <Route path="/meeting/:id" element={<MeetingPage />} />
                <Route
                    path="/rankings/:year/:event/:ageGroup/:sex"
                    element={<RankingPage />}
                />
                <Route path="/headtohead" element={<HeadToHeadPage />} />
                <Route path="/news" element={<NewsPage />} />
                <Route path="/contact" element={<Contact />} />
            </Routes>
        </div>
    </div>
);

export default App;
