import { useSelector } from "react-redux";
import Table from "../../components/Table/Table";
import { MEDAL } from "../../constants";
import { getChampionshipMedals } from "../../queries";
import { Athlete, Performance } from "../../types";
import { getSelectedEvent } from "../../selectors/view";

interface HonoursTableProps {
    athlete: Athlete;
    performances: Performance[];
}

const HonoursTable = ({ athlete, performances }: HonoursTableProps) => {
    const selectedEvent = useSelector(getSelectedEvent);
    const medals = getChampionshipMedals(
        performances.filter(
            ({ event }) => event === selectedEvent || "ALL" === selectedEvent
        )
    );
    if (!Object.keys(medals).length) {
        return null;
    }
    const data = Object.entries(medals).reduce(
        (acc: [string[], string[]], [champs, meds]) => {
            acc[0].push(champs);
            acc[1].push(meds.toString());
            return acc;
        },
        [[], []]
    );
    return (
        <Table
            headers={["Championship", "Honour"]}
            data={data}
            title={"Honours"}
        />
    );
};

export default HonoursTable;
