import { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { useParams } from "react-router-dom";
import LoadingIcon from "../../components/LoadingIcon/LoadingIcon";
import AthleteEventCard from "./AthleteEventCard";
import AthletePagePerformanceRow from "./AthletePagePerformanceRow";
import MedalTally from "./MedalTally";
import SectionsCarousel from "./SectionsCarousel";
import { RootState } from "../../slices";
import { fetchAthleteData } from "../../thunks";
import {
    getAthleteById,
    getAthleteIds,
    getMeetings,
    getPerformances,
    getRankingsByAthleteId,
    getVenues,
} from "../../selectors/analysis";
import {
    getChampionshipMedals,
    getEventStatistics,
    getPerformancesByEvent,
    getYearOfDate,
} from "../../queries";
import { Performance, EventStatistics, Ranking } from "../../types";
import { MEDAL } from "../../constants";
import { sortPerformancesByDate } from "../../sorts";
import styles from "./athlete.module.css";
import { isMobile } from "../../App";
import OverviewDesktop from "./OverviewDesktop";
import { getSeason, getSelectedEvent } from "../../selectors/view";
import { actions } from "../../slices/view";

const AthletePage = () => {
    const dispatch = useDispatch();
    const { id } = useParams();
    const allMeetings = useSelector(getMeetings);
    const allVenues = useSelector(getVenues);
    const selectedEvent = useSelector(getSelectedEvent);
    const athlete = useSelector((state: RootState) =>
        getAthleteById(state, id)
    );
    const allPerformances = useSelector(getPerformances);
    const rankings = useSelector((state: RootState) =>
        getRankingsByAthleteId(state, id)
    );
    const [performances, setPerformances] = useState<Performance[]>([]);
    const fetchedAthleteIds = useSelector(getAthleteIds);
    const [eventStats, setEventStats] = useState<
        Record<string, EventStatistics>
    >({});
    const [events, setEvents] = useState<string[]>([]);
    const [medals, setMedals] = useState<Record<string, MEDAL[]>>({});
    const [overall, setOverall] = useState(false);
    const [season, setSeason] = useState(true);
    const [seasons, setSeasons] = useState<string[]>([]);
    const [results, setResults] = useState(false);
    const seasonYear = useSelector(getSeason);
    useEffect(() => {
        if (
            id &&
            !fetchedAthleteIds.includes(id) &&
            allVenues !== undefined &&
            allMeetings !== undefined
        ) {
            fetchAthleteData(id, allMeetings, allVenues);
        }
    }, [id, allMeetings, allVenues, fetchedAthleteIds]);
    useEffect(() => {
        setMedals(
            getChampionshipMedals(
                allPerformances.filter((p) => p.athleteId === id)
            )
        );
        const athletePerformances = allPerformances.filter(
            (p) => p.athleteId === id
        );
        if (athletePerformances.length) {
            dispatch(
                actions.setSeason(
                    getYearOfDate(athletePerformances[0].date).toString()
                )
            );
        }
        setSeasons(
            allPerformances
                .filter(({ athleteId }) => athleteId === id)
                .reduce(
                    (acc: string[], p) => {
                        const year = getYearOfDate(p.date).toString();
                        return acc.includes(year) ? acc : [...acc, year];
                    },
                    ["ALL"]
                )
        );
    }, [allPerformances, id, dispatch]);
    useEffect(() => {
        setPerformances(
            allPerformances.filter(
                (p) =>
                    p.athleteId === id &&
                    (getYearOfDate(p.date).toString() === seasonYear ||
                        !season ||
                        seasonYear === "ALL") &&
                    (selectedEvent === "ALL" || selectedEvent === p.event)
            )
        );
    }, [allPerformances, id, season, seasonYear, selectedEvent]);
    useEffect(() => {
        if (performances.length) {
            const eventsAHasDone = performances
                .map(({ event }) => event)
                .reduce(
                    (acc: string[], event) =>
                        acc.includes(event) ? acc : [...acc, event],
                    []
                );
            setEvents(eventsAHasDone);
            setEventStats(
                getEventStatistics(getPerformancesByEvent(performances))
            );
        }
    }, [performances]);

    const Overview = ({ isSeasonOverview }: { isSeasonOverview: boolean }) => {
        if (Object.keys(eventStats).length === 0) {
            return null;
        }
        return (
            <>
                {isSeasonOverview && (
                    <div className={styles.seasonSelector}>
                        {seasons.map((year) => (
                            <div
                                className={`${styles.seasonYear} ${
                                    seasonYear === year ? styles.selected : ""
                                } `}
                                onClick={() =>
                                    dispatch(actions.setSeason(year))
                                }
                            >
                                {year}
                            </div>
                        ))}
                    </div>
                )}
                {events
                    .sort(
                        (eventA, eventB) =>
                            performances.filter((p) => p.event === eventB)
                                .length -
                            performances.filter((p) => p.event === eventA)
                                .length
                    )
                    .map((event) => (
                        <AthleteEventCard
                            name={event}
                            statistics={eventStats[event]}
                            performances={performances.filter(
                                (p) => p.event === event
                            )}
                            rankings={rankings.reduce(
                                (acc, r): Record<string, Ranking> => {
                                    if (event === r.event) {
                                        return { [r.year]: r, ...acc };
                                    }
                                    return acc;
                                },
                                {}
                            )}
                            showRanking={isSeasonOverview}
                        />
                    ))}
            </>
        );
    };

    const PerformancesList = ({
        performances,
    }: {
        performances: Performance[];
    }) => {
        return (
            <>
                {performances.sort(sortPerformancesByDate).map((p) => (
                    <AthletePagePerformanceRow
                        performance={p}
                        eventName={p.event}
                        venue={allVenues.find((v) => v.id === p.venueId)}
                    />
                ))}
            </>
        );
    };

    return (
        <>
            {performances.length === 0 && athlete === undefined && (
                <LoadingIcon />
            )}
            <div className={styles.athletePage}>
                {athlete !== undefined && (
                    <>
                        <div className={styles.athleteDetails}>
                            <div className={styles.name}>{athlete.name}</div>
                            <div>
                                <div className={styles.club}>
                                    {athlete.club}
                                </div>
                                <div className={styles.subDetails}>
                                    <div className={styles.ageGroup}>
                                        {athlete.ageGroup}
                                    </div>
                                    <div className={styles.sex}>
                                        {athlete.sex}
                                    </div>
                                </div>
                            </div>
                        </div>
                        {isMobile && <MedalTally medals={medals} />}
                        {isMobile && (
                            <div className={styles.athleteStats}>
                                <SectionsCarousel
                                    overall={overall}
                                    season={season}
                                    results={results}
                                    setOverall={setOverall}
                                    setSeason={setSeason}
                                    setResults={setResults}
                                />
                                <div className={styles.statisticsContent}>
                                    {(overall || season) && (
                                        <Overview isSeasonOverview={season} />
                                    )}
                                    {results && (
                                        <PerformancesList
                                            performances={performances}
                                        />
                                    )}
                                </div>
                            </div>
                        )}
                        {!isMobile && (
                            <OverviewDesktop
                                performances={allPerformances.filter(
                                    ({ athleteId, event, date }) =>
                                        athleteId === athlete.id &&
                                        (selectedEvent === "ALL" ||
                                            selectedEvent === event) &&
                                        (seasonYear ===
                                            getYearOfDate(date).toString() ||
                                            seasonYear === "ALL")
                                )}
                                events={allPerformances
                                    .filter(
                                        ({ athleteId }) =>
                                            athleteId === athlete.id
                                    )
                                    .map(({ event }) => event)
                                    .reduce(
                                        (acc: string[], event) =>
                                            acc.includes(event)
                                                ? acc
                                                : [...acc, event],
                                        []
                                    )}
                                venues={allVenues}
                                seasons={seasons}
                            />
                        )}
                    </>
                )}
            </div>
        </>
    );
};

export default AthletePage;
