import { useEffect, useState } from "react";
import { useSelector } from "react-redux";
import { isMobile } from "../../App";
import Search from "../../components/Search/Search";
import { SearchResultType } from "../../components/Search/Result";
import DataComparisonSection from "./DataComparisonSection";
import SelectAthleteZone from "../../components/SelectAthleteZone/SelectAthleteZone";
import Cross from "../../components/Cross/Cross";
import BestPerformanceTable from "./BestPerformancesTable";
import HonoursTable from "./HonoursTable";
import { fetchAthleteData, fetchAthleteSearchResults } from "../../thunks";
import {
    getAthleteById,
    getAthleteIds,
    getMeetings,
    getPerformances,
    getVenues,
} from "../../selectors/analysis";
import {
    getAthleteHeadToHead,
    getHeadToHeadPerformances,
} from "../../analysis";
import { RootState } from "../../slices";
import { Athlete, Performance, AthleteSearchResult } from "../../types";
import styles from "./headToHead.module.css";

const HeadToHeadPage = () => {
    const [athleteIds, setAthleteIds] = useState<
        Record<string, string | undefined>
    >({
        A: undefined,
        B: undefined,
    });
    const fetchedAthleteIds = useSelector(getAthleteIds);
    const [showSearch, setShowSearch] = useState(true);
    const allMeetings = useSelector(getMeetings);
    const allVenues = useSelector(getVenues);
    const allPerformances = useSelector(getPerformances);
    const athleteA = useSelector((state: RootState) =>
        getAthleteById(state, athleteIds.A)
    );
    const athleteB = useSelector((state: RootState) =>
        getAthleteById(state, athleteIds.B)
    );
    const [comparisonSheet, setComparisonSheet] = useState<
        Record<string, Record<string, Record<string, number>>>
    >({});
    const [headToHeadPerformances, setHeadToHeadPerformances] = useState<
        Performance[][]
    >([]);

    const handleResultClick = (id: string) => () => {
        if (!fetchedAthleteIds.includes(id)) {
            fetchAthleteData(id, allMeetings, allVenues);
        }
        if (athleteIds.A && athleteIds.A !== id) {
            setAthleteIds({ ...athleteIds, B: id });
        } else if (athleteIds.B !== id) {
            setAthleteIds({ ...athleteIds, A: id });
        }
    };

    const handleAthleteCardClick =
        (id: string) => (_e: React.MouseEvent<HTMLElement>) => {
            if (athleteIds.A && athleteIds.A !== id) {
                setAthleteIds({ ...athleteIds, B: id });
            } else if (athleteIds.B !== id) {
                setAthleteIds({ ...athleteIds, A: id });
            }
        };

    const handleUnselectAthlete =
        (side: string) => (_e: React.MouseEvent<HTMLElement>) => {
            setAthleteIds({ ...athleteIds, [side]: undefined });
            setShowSearch(true);
        };

    useEffect(() => {
        if (athleteA && athleteB) {
            setShowSearch(false);
            setComparisonSheet(
                getAthleteHeadToHead(
                    [athleteA.id, athleteB.id],
                    allPerformances
                )
            );
            setHeadToHeadPerformances(
                getHeadToHeadPerformances(
                    [athleteA.id, athleteB.id],
                    allPerformances
                )
            );
        } else {
            setComparisonSheet({});
            setHeadToHeadPerformances([]);
        }
    }, [athleteA, athleteB, allPerformances]);

    const dataToResultFormat = (
        data: AthleteSearchResult[]
    ): SearchResultType[] =>
        data.map((d) => ({
            id: d.id,
            primary: d.name,
            secondary: d.club,
            sub: d.ageGroup,
        }));

    const AthleteInfo = ({
        athlete,
        side,
    }: {
        athlete?: Athlete;
        side: "A" | "B";
    }) =>
        athlete ? (
            <>
                <div key={athlete.id} className={styles.athleteInformation}>
                    <div className={styles.name}>{athlete.name}</div>
                    <div className={styles.subDetails}>
                        <div className="age-group">{athlete.ageGroup}</div>
                        <div className="club">
                            {athlete.club.split("/").map((c) => (
                                <div key={c}>{c}</div>
                            ))}
                        </div>
                    </div>
                </div>
                <Cross onClick={handleUnselectAthlete(side)} />
            </>
        ) : (
            <div className={styles.athleteInformation}>Search an athlete</div>
        );

    return (
        <div className={styles.headToHead}>
            {!isMobile && (
                <SelectAthleteZone
                    selectedIds={athleteIds}
                    onAthleteCardClick={handleAthleteCardClick}
                />
            )}
            <div className={styles.athletes}>
                <div
                    className={styles.athlete}
                    style={{ border: "2px solid tomato" }}
                >
                    <AthleteInfo athlete={athleteA} side={"A"} />
                </div>
                <div
                    className={styles.athlete}
                    style={{ border: "2px solid #6347ff" }}
                >
                    <AthleteInfo athlete={athleteB} side={"B"} />
                </div>
            </div>
            <div className={styles.comparison}>
                {athleteA && athleteB && (
                    <>
                        {!isMobile && (
                            <div className={styles.extraComparisonColumn}>
                                <HonoursTable
                                    athlete={athleteA}
                                    performances={allPerformances.filter(
                                        ({ athleteId }) =>
                                            athleteId === athleteA.id
                                    )}
                                />
                                <BestPerformanceTable
                                    athleteA={athleteA}
                                    athleteB={athleteB}
                                    performances={allPerformances}
                                />
                            </div>
                        )}
                        <div
                            className={`${styles.statistics} ${
                                !isMobile ? styles.statisticsDesktop : ""
                            }`}
                        >
                            <DataComparisonSection
                                athleteA={athleteA}
                                athleteB={athleteB}
                                comparisonSheet={comparisonSheet}
                                headToHeadPerformances={headToHeadPerformances}
                            />
                        </div>
                        {!isMobile && (
                            <div className={styles.extraComparisonColumn}>
                                <HonoursTable
                                    athlete={athleteB}
                                    performances={allPerformances.filter(
                                        ({ athleteId }) =>
                                            athleteId === athleteB.id
                                    )}
                                />
                            </div>
                        )}
                    </>
                )}
                {isMobile && showSearch && (
                    <Search
                        headers={["Name", "Club"]}
                        fetchAction={fetchAthleteSearchResults}
                        dataToResultFormat={dataToResultFormat}
                        entity="athlete"
                        handleResultClick={handleResultClick}
                    />
                )}
            </div>
        </div>
    );
};

export default HeadToHeadPage;
