import styles from "./sectionsCarousel.module.css";

const SectionsCarousel = ({
    overall,
    season,
    results,
    setOverall,
    setSeason,
    setResults,
}: {
    overall: boolean;
    season: boolean;
    results: boolean;
    setOverall: (v: boolean) => void;
    setSeason: (v: boolean) => void;
    setResults: (v: boolean) => void;
}) => {
    const handleSectionSelect = (section: string) => () => {
        setOverall(section === "overall");
        setSeason(section === "season");
        setResults(section === "results");
    };

    return (
        <div className={styles.sectionsCarousel}>
            <div
                className={`overall-section ${
                    overall ? `${styles.selected}` : ""
                }`}
                onClick={handleSectionSelect("overall")}
            >
                All Time
            </div>
            <div
                className={`seasonal-section ${
                    season ? `${styles.selected}` : ""
                }`}
                onClick={handleSectionSelect("season")}
            >
                Seasonal
            </div>
            <div
                className={`results-section ${
                    results ? `${styles.selected}` : ""
                }`}
                onClick={handleSectionSelect("results")}
            >
                Results
            </div>
        </div>
    );
};

export default SectionsCarousel;
