import styles from "./search.module.css";

export interface SearchResultType {
    id: string;
    primary: string | number;
    secondary?: string | number;
    sub?: string | number;
    entity?: string;
    onResultClick?: () => void;
}

const Result = ({
    id,
    primary,
    secondary,
    sub,
    entity,
    onResultClick,
}: SearchResultType) => (
    // eslint-disable-next-line jsx-a11y/anchor-is-valid
    <a
        className={styles.result}
        href={!onResultClick ? `#/${entity}/${id}` : undefined}
        onClick={onResultClick}
        key={id}
    >
        <div className={styles.resultMainRow}>
            <div className={styles.resultPrimary}>{primary}</div>
            <div className={styles.resultSecondary}>{secondary}</div>
        </div>
        <div className={styles.resultSubRow}>
            <div className={styles.resultSub}>{sub}</div>
        </div>
    </a>
);

export default Result;
