import styles from "./table.module.css";

interface TableProps {
    headers: string[];
    data: (string | number)[][];
    title?: string;
}

const Table = ({ headers, data, title }: TableProps) => {
    const tableWidth = headers.length;
    const tableLength = data[0].length;
    const dataAsRows: (string | number)[][] = [];
    for (let i = 0; i < tableLength; i++) {
        dataAsRows.push([]);
        for (let j = 0; j < tableWidth; j++) {
            dataAsRows[i].push(data[j][i]);
        }
    }
    return (
        <div className={styles.container}>
            {title && <div className={styles.title}>{title}</div>}
            <table className={styles.table} cellSpacing={"none"}>
                <thead>
                    <tr className={styles.header}>
                        {headers.map((header) => (
                            <th>{header}</th>
                        ))}
                    </tr>
                </thead>
                <tbody>
                    {dataAsRows.map((row, i) => (
                        <tr key={i} className={styles.row}>
                            {row.map((cell) => (
                                <td>{cell}</td>
                            ))}
                        </tr>
                    ))}
                </tbody>
            </table>
        </div>
    );
};

export default Table;
