import { useState } from "react";
import {
    formatShortDate,
    formatPerformance,
    formatPosition,
} from "../../formatters";
import { Performance, Venue } from "../../types";
import styles from "./athletePagePerformanceRow.module.css";

const AthletePagePerformanceRow = ({
    performance,
    eventName,
    venue,
}: {
    performance: Performance;
    eventName?: string;
    venue?: Venue;
}) => {
    const [isExpanded, setIsExpanded] = useState(false);
    return (
        <>
            <div
                className={`${styles.performanceRow} ${
                    isExpanded ? styles.selected : ""
                }`}
                onClick={() => setIsExpanded(!isExpanded)}
            >
                <div className={styles.event}>{eventName}</div>
                <div className={styles.meeting}>{performance.meeting}</div>
                <div className={styles.performance}>
                    {formatPerformance(performance.performance, eventName)}
                </div>
                <div className={styles.date}>
                    {formatShortDate(performance.date)}
                </div>
            </div>
            <div
                className={`${styles.performanceRowExpanded} ${
                    isExpanded ? "" : styles.hidden
                }`}
            >
                <div className={styles.expandedStatistics}>
                    <div className={styles.statisticRow}>
                        <div>Venue:</div>
                        <div>{venue?.name}</div>
                    </div>
                    <div className={styles.statisticRow}>
                        <div>Position:</div>
                        <div>{formatPosition(performance.position)}</div>
                    </div>
                    {performance.heat !== "" && (
                        <div className={styles.statisticRow}>
                            <div>Heat:</div>
                            <div>{performance.heat}</div>
                        </div>
                    )}
                    {performance.wind !== 0 && (
                        <div className={styles.statisticRow}>
                            <div>Wind:</div>
                            <div>{performance.wind}</div>
                        </div>
                    )}
                </div>
            </div>
        </>
    );
};

export default AthletePagePerformanceRow;
