import { createSlice } from "@reduxjs/toolkit";

export interface ViewState {
    selectedEvent: string;
    season: string;
}

const initialState: ViewState = {
    selectedEvent: "ALL",
    season: "ALL",
};

interface SetAction<PayloadType> {
    type: string;
    payload: PayloadType;
}

const view = createSlice({
    name: "view",
    initialState,
    reducers: {
        setSelectedEvent: (state, action: SetAction<string>) => {
            state.selectedEvent = action.payload;
        },
        setSeason: (state, action: SetAction<string>) => {
            state.season = action.payload;
        },
    },
});

export const actions = view.actions;

export default view.reducer;
