import { useParams } from "react-router-dom";
import { fetchRankingData } from "../../thunks";
import { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { getError, getRankings } from "../../selectors/ranking";
import RankingRow from "../../components/Rows/RankingRow/RankingRow";
import LoadingIcon from "../../components/LoadingIcon/LoadingIcon";
import DropdownSelector from "../../components/DropdownSelector/DropdownSelector";
import { EVENTS } from "../../constants";
import { actions } from "../../slices/ranking";
import Modal from "../../components/Modal/Modal";
import Button from "../../components/Button/Button";
import styles from "./ranking.module.css";

const RankingPage = () => {
    const dispatch = useDispatch();
    const { event, ageGroup, sex, year } = useParams();
    const rankings = useSelector(getRankings);
    const error = useSelector(getError);

    const [thisEvent, setEvent] = useState(event ?? "100");
    const [thisYear, setYear] = useState(year ?? "2023");
    const [thisSex, setSex] = useState(sex ?? "M");
    const [thisAgeGroup, setAgeGroup] = useState(ageGroup ?? "ALL");

    useEffect(() => {
        if (event && ageGroup && sex && year) {
            dispatch(actions.setError(null));
            fetchRankingData(event, ageGroup, sex, year);
        }
    }, [event, ageGroup, sex, year]);

    useEffect(() => {
        if (thisYear && thisEvent && thisAgeGroup && thisSex) {
            window.history.replaceState(
                null,
                "",
                `#/rankings/${thisYear}/${thisEvent}/${thisAgeGroup}/${thisSex}`
            );
            dispatch(actions.setRankings([]));
            fetchRankingData(thisEvent, thisAgeGroup, thisSex, thisYear);
        }
    }, [thisYear, thisEvent, thisAgeGroup, thisSex]);

    const dismissErrorAndReset = () => {
        setEvent("100");
        setYear("2023");
        setSex("M");
        setAgeGroup("All");
        dispatch(actions.setError(null));
    };

    return (
        <div className={styles.rankingPage}>
            {error && (
                <Modal title={"Error"} closeAction={dismissErrorAndReset}>
                    Oops!
                    <div>{error}</div>
                    <Button text="Ok" onClick={dismissErrorAndReset} />
                </Modal>
            )}
            <div className={styles.title}>Rankings</div>
            <div className={styles.selectorContainer}>
                <DropdownSelector
                    selectedOption={thisYear}
                    text="Year"
                    options={[
                        "2023",
                        "2022",
                        "2021",
                        "2020",
                        "2019", // obvs update this to be sensible
                        "2018",
                        "2017",
                        "2016",
                        "2015",
                        "2014",
                        "2013",
                        "2012",
                        "2011",
                        "2010",
                        "2009",
                        "2008",
                        "2007",
                        "2006",
                    ]}
                    onSelectAction={(id) => setYear(id)}
                />
                <DropdownSelector
                    selectedOption={thisEvent}
                    text="Event"
                    options={EVENTS}
                    onSelectAction={(id) => setEvent(id)}
                />
                <DropdownSelector
                    selectedOption={thisAgeGroup}
                    text="Age Group"
                    options={["ALL", "U23", "U20", "U17", "U15", "U13", "U11"]}
                    onSelectAction={(id) => setAgeGroup(id)}
                />
                <DropdownSelector
                    selectedOption={thisSex}
                    text="Sex"
                    options={["M", "W"]}
                    onSelectAction={(id) => setSex(id)}
                />
            </div>
            <div className={styles.list}>
                {rankings.length === 0 && <LoadingIcon />}
                {rankings.length > 0 &&
                    rankings.map((ranking) => (
                        <RankingRow ranking={ranking} event={thisEvent} />
                    ))}
            </div>
        </div>
    );
};

export default RankingPage;
