import { formatPerformance } from "../../../formatters";
import { RankingListRow } from "../../../types";
import styles from "./rankingRow.module.css";

interface RankingRowProps {
    ranking: RankingListRow;
    event: string;
}

const RankingRow = ({ ranking, event }: RankingRowProps) => (
    <div className={styles.row}>
        <div className={styles.left}>{ranking.rank}</div>
        <div className={styles.middle}>
            <a className={styles.name} href={`#/athlete/${ranking.id}`}>
                {ranking.name}
            </a>
            <div className={styles.club}>{ranking.club}</div>
        </div>
        <div className={styles.right}>
            {formatPerformance(ranking.performance, event)}
        </div>
    </div>
);

export default RankingRow;
