import { useState } from "react";
import {
    formatPercentage,
    formatPerformance,
    formatPosition,
} from "../../formatters";
import { EventStatistics, Ranking, Performance } from "../../types";
import { useSelector } from "react-redux";
import { getVenueById } from "../../selectors/analysis";
import Chart from "../../components/Chart/Chart";
import { getYearOfDate, validLegalPerformanceFilter } from "../../queries";
import styles from "./athleteEventCard.module.css";
import { RootState } from "../../slices";

const AthleteEventCard = ({
    name,
    statistics,
    performances,
    rankings,
    showRanking = false,
}: {
    name: string;
    statistics: EventStatistics;
    performances: Performance[];
    rankings: Record<string, Ranking>;
    showRanking?: boolean;
}) => {
    const [isExpanded, setIsExpanded] = useState(false);
    const venue = useSelector((state: RootState) =>
        getVenueById(state, statistics.mostPopularVenueId)
    );
    const currentYear = getYearOfDate(performances[0]?.date);
    const hasPreviousYear = !!rankings[currentYear - 1];
    const hasCurrentYear = !!rankings[currentYear];
    const rankingTickArrow = (rankings: Record<string, Ranking>) =>
        hasPreviousYear &&
        rankings[currentYear - 1]?.rank - rankings[currentYear]?.rank > 0
            ? "\u2191"
            : "\u2193";
    const getTickColour = () => {
        if (!hasPreviousYear) {
            return undefined;
        }
        if (rankings[currentYear - 1]?.rank - rankings[currentYear]?.rank > 0) {
            return "green";
        } else if (
            rankings[currentYear - 1]?.rank - rankings[currentYear]?.rank <
            0
        ) {
            return "red";
        }
    };

    return (
        <>
            <div
                className={`${styles.athleteEventCard} ${
                    isExpanded ? styles.selected : ""
                }`}
                onClick={() => setIsExpanded(!isExpanded)}
            >
                <div className={styles.mainRow}>
                    <div className={styles.rowLeft}>
                        <div className={styles.name}>{name}</div>
                    </div>
                    <div className={styles.rowRight}>
                        {showRanking && (
                            <div className={styles.rankingDetails}>
                                <div className={styles.currentRank}>
                                    <div>
                                        {rankings[currentYear]?.rank
                                            ? formatPosition(
                                                  rankings[currentYear]?.rank
                                              )
                                            : ""}
                                    </div>
                                    <div className={styles.rankLabel}>
                                        {rankings[currentYear]?.rank
                                            ? "Ranked"
                                            : `Not\nRanked`}
                                    </div>
                                </div>
                                {hasPreviousYear && hasCurrentYear && (
                                    <div
                                        className={styles.rankingTick}
                                        style={{ color: getTickColour() }}
                                    >
                                        {rankings[currentYear - 1]?.rank -
                                            rankings[currentYear]?.rank !==
                                        0
                                            ? `${rankingTickArrow(
                                                  rankings
                                              )}${Math.abs(
                                                  rankings[currentYear - 1]
                                                      ?.rank -
                                                      rankings[currentYear]
                                                          ?.rank
                                              )}`
                                            : null}
                                    </div>
                                )}
                            </div>
                        )}
                        <div
                            className="pb"
                            style={
                                showRanking
                                    ? {}
                                    : { width: "100%", textAlign: "right" }
                            }
                        >
                            {formatPerformance(statistics?.best, name)}
                        </div>
                    </div>
                </div>
                <div className={styles.bottomRow}>
                    <div className={styles.cardExpandCollapseButton}>
                        {isExpanded ? "Collapse" : "Expand"}
                    </div>
                </div>
            </div>
            <div
                className={`${styles.eventCardExpanded} ${
                    isExpanded ? "" : styles.hidden
                }`}
            >
                {isExpanded && (
                    <div className={styles.expandedStatistics}>
                        <div className={styles.statisticRow}>
                            <div>Win Rate:</div>
                            <div>{formatPercentage(statistics.winRate)}</div>
                        </div>
                        <div className={styles.statisticRow}>
                            <div>Top Three Rate:</div>
                            <div>
                                {formatPercentage(statistics.topThreeRate)}
                            </div>
                        </div>
                        <div className={styles.statisticRow}>
                            <div>Win Count:</div>
                            <div>{statistics.winCount}</div>
                        </div>
                        <div className={styles.statisticRow}>
                            <div>Longest Win Streak:</div>
                            <div>{statistics.longestWinStreak}</div>
                        </div>
                        <div className={styles.statisticRow}>
                            <div>Most Common Venue:</div>
                            <div>{venue?.name ?? "-"}</div>
                        </div>
                        <div className={styles.statisticRow}>
                            <div>Total Performances:</div>
                            <div>{statistics.totalPerformances}</div>
                        </div>
                        {performances.filter(validLegalPerformanceFilter)
                            .length > 0 && (
                            <>
                                <div>Progression Chart</div>
                                <Chart
                                    event={name}
                                    performances={performances.filter(
                                        (p) => !Number.isNaN(p.performance)
                                    )}
                                    isForSeason={showRanking}
                                />
                            </>
                        )}
                    </div>
                )}
            </div>
        </>
    );
};

export default AthleteEventCard;
